/* Modal */

.ReactModal__Body--open .mymodal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  border: 1px solid #ccc;
  background: #fff;
  overflow: auto;
  border-radius: 14px;
  outline: none;
  padding: 10px 20px 30px;
  width: 40vw;
}

.ReactModal__Body--open .mymodal p {
  font-size: 15px;
}

.ReactModal__Body--open .myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

.ReactModal__Content--after-open .close-modal_container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding-bottom: 10px;
}

.ReactModal__Content--after-open .close-modal-btn {
  border: none;
  background: transparent;
  font-size: 20px;
  margin-top: 5px;
}
